.navbar {
  background-color: #2ac977;
  color: #fff;
  padding: 20px;
  display: flex;
  justify-content: space-between; /* Align items horizontally with space between them */
  align-items: center; /* Align items vertically in the center */
}

.navbar h1 {
  margin: 0;
  padding: 0;
}

.navbar ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex; /* Display list items as flex items */
}

.navbar ul li {
  margin-right: 5px; /* Add 5px margin between list items */
}

.navbar ul li:last-child {
  margin-right: 0; /* Remove margin from the last list item */
}

.navbar ul li a {
  text-decoration: none;
  color: #fff;
  padding: 10px;
  border-radius: 5px;
  background-color: #007bff;
}

.navbar ul li a:hover {
  background-color: #0056b3;
}

.navbar ul li:first-child {
  margin-left: auto; /* Push the first item to the left */
}
